
import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Divider, TextField, Typography } from '@material-ui/core';
import useMediaQuery from '@material-ui/core/useMediaQuery';

import { calculateValue } from 'Utils/Calculate'


const useStyles = makeStyles((theme) => ({
  root: {
    padding: '.5rem',
    boxSizing: 'border-box',
    display: 'grid',
    gridGap: '.5rem',
    height: '100%',
    // –¥–æ 599px ~ –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–∞—è –æ—Ä–∏–µ–Ω—Ç–∞—Ü–∏—è 
    [theme.breakpoints.down('xs')]: {
      gridAutoRows: 'max-content',
    },
    // –ø–æ—Å–ª–µ 600px ~ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∞—è –æ—Ä–∏–µ–Ω—Ç–∞—Ü–∏—è 
    [theme.breakpoints.up('sm')]: {
      gridTemplateColumns: ' 1fr max-content 1fr',
    },
    '& .MuiDivider-root': {
      [theme.breakpoints.down('xs')]: {
        margin: '1rem',
      },
    },
    '& #output-text': {
      color: 'green'
    }
  },
  CPCF: {
    display: 'grid',
    gridTemplateRows: 'repeat(4, max-content)',
    gridTemplateColumns: 'repeat(2, max-content)',
    gridGap: ".5rem",
    alignItems: 'center',
    justifyContent: 'center',
  },
  info: {
    display: 'grid',
    rowGap: '.5em',
  },
  titleOutput: {
    display: 'grid',
    gridTemplateColumns: 'repeat(3, max-content)',
    gridGap: '.5em',
    alignItems: 'center',
    justifyContent: 'center',
    '& .MuiTextField-root': {
      width: '100px',
    }
  },
  titleInput: {
    display: 'grid',
    alignItems: 'center',
    justifyContent: 'center',
  },
}));

const OUTPUT = 'output';
const CALORIE = 'calorie';
const PROTEIN = 'protein';
const CARBOHYDRATE = 'carbohydrate';
const FAT = 'fat';

export default function Calorie({ id }) {
  const classes = useStyles();
  const theme = useTheme();
  const isSizeSM = useMediaQuery(theme.breakpoints.up('sm'));

  const [inputValues, setInputValues] = React.useState({
    [CALORIE]: "", // Calorie - –∫–∞–ª–æ—Ä–∏–∏
    [PROTEIN]: "", // Protein - –±–µ–ª–∫–∏
    [CARBOHYDRATE]: "", // Carbohydrates - —É–≥–ª–µ–≤–æ–¥—ã
    [FAT]: "", // Fat - –∂–∏—Ä—ã
  });
  const [outputWeight, setOutputWeight] = React.useState("");

  const outputState = React.useMemo(() => {
    const calculate = (value) => calculateValue({ myDivisible: value, initialDivisible: outputWeight, divisor: 100 })
    return ({
      [CALORIE]: calculate(inputValues[CALORIE]),
      [PROTEIN]: calculate(inputValues[PROTEIN]),
      [CARBOHYDRATE]: calculate(inputValues[CARBOHYDRATE]),
      [FAT]: calculate(inputValues[FAT]),
    })
  }, [inputValues, outputWeight]);

  const onChangeInputValues = React.useCallback((e) => {
    const { name, value } = e.target;
    setInputValues((prev) => ({ ...prev, [name]: value }));
  }, []);

  const onChangeOutputWeight = React.useCallback((e) => {
    const { value } = e.target;
    setOutputWeight(value);
  }, []);


  return (
    <div className={classes.root}>
      <div className={classes.info}>
        <Typography component="span" className={classes.titleInput}>–ù–∞ 100 –≥ –ø—Ä–æ–¥—É–∫—Ç–∞</Typography>
        <CPCF values={inputValues} onChange={onChangeInputValues} />
      </div>
      <Divider variant="middle" orientation={isSizeSM ? 'vertical' : 'horizontal'} />
      <div className={classes.info}>
        <div className={classes.titleOutput}>
          <Typography component="span">–ù–∞</Typography>
          <TextField
            type="number"
            name="weight"
            placeholder="–≤–µ—Å"
            variant="outlined"
            size="small"
            error={!outputWeight || !+outputWeight || outputWeight < 0}
            value={outputWeight}
            onChange={onChangeOutputWeight}
            onKeyPress={(e) => e.key === 'Enter' && e.target.blur()}
          />
          <Typography component="span">–≥ –ø—Ä–æ–¥—É–∫—Ç–∞</Typography>
        </div>
        <CPCF type={OUTPUT} values={outputState} />
      </div>
    </div >
  );
}

/** –ö–ë–ñ–£ - –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ –∏–Ω–ø—É—Ç–æ–≤ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö/–ø–æ—Å—á–∏—Ç–∞–Ω–Ω—ã—Ö –ö–ë–ñ–£
 * @param {string} [param.type=input] input or output date
 * @param {object} values –æ–±—ä–µ–∫—Ç –¥–∞–Ω–Ω—ã—Ö: –∫–∫–∞–ª, –±–µ–ª–∫–∏, –±–µ–ª–∫–∏, —É–≥–ª–µ–≤–æ–¥—ã
 * @param {function} onChange —Ñ—É–Ω–∫—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è values
 */
function CPCF({ type = 'input', values = {}, onChange = () => { } }) {
  const classes = useStyles();
  const isOutput = React.useMemo(() => type === OUTPUT, [type]);

  // –∫–ª–∏–∫ –Ω–∞ —ç–Ω—Ç–µ—Ä - –ø–µ—Ä–µ—Ö–æ–¥ –Ω–∞ –Ω–µ–∫—Å—Ç –∏–Ω–ø—É—Ç
  const onKeyPress = React.useCallback((e) => {
    if (e.key === 'Enter') {
      let nextElement = null
      // –≤—Ä—É—á–Ω—É—é –æ–ø—Ä–µ–¥–µ–ª—è–µ–º —Å–ª–µ–¥—É—é—â–∏–π –∏–Ω–ø—É—Ç –¥–ª—è —Ñ–æ–∫—É—Å–∞
      switch (e.target.name) {
        case CALORIE:
          nextElement = document.getElementsByName(PROTEIN)[0]
          break;
        case PROTEIN:
          nextElement = document.getElementsByName(CARBOHYDRATE)[0]
          break;
        case CARBOHYDRATE:
          nextElement = document.getElementsByName(FAT)[0]
          break;
        case FAT:
          nextElement = document.getElementsByName('weight')[0]
          break;
        default: nextElement = null;
      }
      if (nextElement)
        nextElement.focus()
      else
        e.target.blur()
    }
  }, []);

  return (
    <div className={classes.CPCF}>
      <Typography>–ö:</Typography>
      <TextField
        type="number"
        error={(!isOutput && !values[CALORIE]) || values[CALORIE] < 0}
        name={CALORIE}
        id={isOutput ? "output-text" : ""}
        disabled={isOutput}
        size="small"
        placeholder={isOutput ? '0.0' : '–≤–≤–µ–¥–∏—Ç–µ'}
        value={values[CALORIE]}
        onChange={onChange}
        onKeyPress={onKeyPress}
      />
      <Typography component="span">–ë:</Typography>
      <TextField
        type="number"
        error={values[PROTEIN] < 0}
        name={PROTEIN}
        id={isOutput ? "output-text" : ""}
        disabled={isOutput}
        size="small"
        placeholder={isOutput ? '0.0' : '–≤–≤–µ–¥–∏—Ç–µ'}
        value={values[PROTEIN]}
        onChange={onChange}
        onKeyPress={onKeyPress}
      />
      <Typography>–ñ:</Typography>
      <TextField
        type="number"
        name={CARBOHYDRATE}
        error={values[CARBOHYDRATE] < 0}
        id={isOutput ? "output-text" : ""}
        disabled={isOutput}
        size="small"
        placeholder={isOutput ? '0.0' : '–≤–≤–µ–¥–∏—Ç–µ'}
        value={values[CARBOHYDRATE]}
        onChange={onChange}
        onKeyPress={onKeyPress}
      />
      <Typography>–£:</Typography>
      <TextField
        type="number"
        name={FAT}
        error={values[FAT] < 0}
        id={isOutput ? "output-text" : ""}
        disabled={isOutput}
        size="small"
        placeholder={isOutput ? '0.0' : '–≤–≤–µ–¥–∏—Ç–µ'}
        value={values[FAT]}
        onChange={onChange}
        onKeyPress={onKeyPress}
      />
    </div>
  )
}
